import { Injectable } from '@angular/core';
import { AppConstants } from 'src/app/shared/config/app.constant';
import { HttpClient } from '@angular/common/http';
import { FetchUserForSingleAccClosureReqDTO } from '../account-closure/FetchUserForSingleAccClosureReqDTO';
import * as FileSaver from 'file-saver';
import { Workbook } from 'exceljs';

import * as XLSX from 'xlsx';
import { ProcessAccountClosureRespDTO } from 'src/app/shared/models/process-account-closure-resp-dto';
export const API_URL = AppConstants.acctCloserUrl;

const EXCEL_TYPE = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
const EXCEL_EXTENSION = '.xlsx';
@Injectable({
  providedIn: 'root'
})
export class ProcessAccClosureService {

  constructor(private http: HttpClient) { }


  gettingAllProcessedRecord(user_id) {

    console.log(user_id);
    return this.http.get<FetchUserForSingleAccClosureReqDTO>(`${API_URL}/api2/getAllProcessedRecordsForBulkAndSingle/${user_id}`);
  }

  approveRecord(fileName: string, accountType: string, inputBy: string, id: number, closureType: string,approveReasonTextArea:string) {
    // return this.http.get<FetchUserForSingleAccClosureReqDTO>(`${API_URL}/medAccount/closeAccount/${fileName}/${accountType}/${inputBy}/${id}/${closureType}`);
    return this.http.get<ProcessAccountClosureRespDTO>(`${API_URL}/medAccount/closeAccount/${fileName}/${accountType}/${inputBy}/${id}/${closureType}/${approveReasonTextArea}`);
  }


  approveRecordForSingle(accountType: string, inputBy: string, closureType: string, id: number,approveReasonTextArea:string) {
    console.log(accountType, closureType, id, inputBy)
    return this.http.get<ProcessAccountClosureRespDTO>(`${API_URL}/medAccount/closeAccountSingle/${accountType}/${inputBy}/${closureType}/${id}/${approveReasonTextArea}`);
  }

  public exportAsExcel(json: any[], excelFileName: string): void {
    console.log(excelFileName);
    console.log(json);
    const worksheet: XLSX.WorkSheet = XLSX.utils.json_to_sheet(json);
    worksheet.blankRows = false;

    const workbook: XLSX.WorkBook = { Sheets: { 'data': worksheet }, SheetNames: ['data'] };
    const excelBuffer: any = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });

    this.saveAsExcelSingle(excelBuffer, excelFileName);
  }
  private saveAsExcelSingle(buffer: any, fileName: string): void {
    const data: Blob = new Blob([buffer], { type: EXCEL_TYPE });

    FileSaver.saveAs(data, fileName + '_export_' + new Date().getTime() + EXCEL_EXTENSION);
  }
  onClickofViewReport(fileName, accountType) {
    // @GetMapping("/viewReport/{fileName}/{accountType}"
    return this.http.get<any>(`${API_URL}/file/viewReport/${fileName}/${accountType}`);
  }
  onClickofViewReportSingle(accountType, inputBy, closureType, id) {
    return this.http.get<any>(`${API_URL}/api2/viewReportForSingleAcc/${accountType}/${inputBy}/${closureType}/${id}`)
  }
  onBulkReject(fileName, accountType, inputBy,id,rejectReasonTextArea) {
    console.log(fileName, accountType, inputBy,id,rejectReasonTextArea)
    return this.http.get(`${API_URL}/file/rejectBulk/${accountType}/${fileName}/${inputBy}/${id}/${rejectReasonTextArea}`)
  }
  onSingleReject(accountNumber: string, accntType: string, closure: string, maker: string,rejectReasonTextArea) {
    console.log("in service", accountNumber, accntType, closure, maker)
    return this.http.get(`${API_URL}/api2/rejectSingle/${accountNumber}/${accntType}/${closure}/${maker}/${rejectReasonTextArea}`)
  }
}
